<template>
  <transition name="fade">
  <div @click="onClick">
      <table class="ui inverted blue fixed table">
        <thead>
          <tr>
            <th class="center aligned one wide">#</th>
            <th class="seven wide">Answer</th>
            <th class="two wide">Date</th>
            <th class="three wide">Answerer</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(answer, index) in rowData.answers">
            <td class="center aligned">{{index + 1}}</td>
            <td>{{answer.text}}</td>
            <td>{{answer.createdOn|formatDate}}</td>
            <td>{{answer.displayName}}</td>
          </tr>
        </tbody>
      </table>
  </div>
</transition>
</template>


<script>

export default {
  props: {
    rowData: {
      type: Object,
      required: true
    },
    rowIndex: {
      type: Number
    }
  },
  methods: {
    onClick (event) {
      console.log('my-detail-row: on-click', event.target)
    }
  },
}
</script>

<style scoped>
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-to /* .fade-leave-active in <2.1.8 */ {
  opacity: 0
}
</style>



