<template>
  <div>
  <filter-bar class="ui segment left aligned"></filter-bar>
  <vuetable ref="vuetable"
    v-bind:api-url="apiUrl"
    :fields="fields"
    pagination-path=""
    @vuetable:pagination-data="onPaginationData"
    detail-row-component="my-detail-row"
    @vuetable:cell-clicked="onCellClicked"
    track-by="_id"
    :append-params="moreParams"
  ></vuetable>
  <vuetable-pagination ref="pagination"
  @vuetable-pagination:change-page="onChangePage"></vuetable-pagination>
</div>
</template>

<script>
import Vue from 'vue'
import Vuetable from 'vuetable-2/src/components/Vuetable'
import moment from 'moment'
import VuetablePagination from 'vuetable-2/src/components/VuetablePagination'
import DetailRow from './DetailRow'
import FilterBar from './FilterBar'

Vue.component('filter-bar', FilterBar)
Vue.component('my-detail-row', DetailRow)

export default {
  components: {
    Vuetable, VuetablePagination, DetailRow
  },
  data () {
    return {
      fields: [
        {
          name: 'sequence',
          title: '#',
          sortField: 'sequence',
          titleClass: 'center aligned one wide',
          dataClass: 'center aligned'
        },
        {
          name: 'text',
          title: 'Question',
          titleClass: 'seven wide'
        },
        {
          name: 'displayName',
          title: 'Questioner',
          sortField: 'displayName',
          titleClass: 'three wide'
        },
        {
          name: 'createdOn',
          title: 'Date',
          callback: 'formatDate|MM-DD-YYYY',
          sortField: 'createdOn',
          titleClass: 'two wide'
        },
        {
          name: 'answers.length',
          title: 'Answers',
          titleClass: 'center aligned',
          dataClass: 'center aligned',
          titleClass: 'one wide'
        }
      ],
      moreParams: {}
    }
  },
  computed: {
    apiUrl () {
      let urlParams = new URLSearchParams(window.location.search);
      let getUrl = this.$store.state.apiUrl + urlParams.get('roomId');
      return getUrl;
    }
  },
  mounted() {
    this.$events.$on('filter-set', eventData => this.onFilterSet(eventData))
    this.$events.$on('filter-reset', e => this.onFilterReset())
  },
  methods: {
    formatDate (value, fmt = 'D MMM YYYY') {
      return (value == null)
        ? ''
        : moment(value, 'YYYY-MM-DD').format(fmt)
    },
    onPaginationData (paginationData) {
      this.$refs.pagination.setPaginationData(paginationData)
    },
    onChangePage (page) {
      this.$refs.vuetable.changePage(page)
    },
    onCellClicked (data, field, event) {
        console.log('cellClicked: ', field.name)
        this.$refs.vuetable.toggleDetailRow(data._id)
    },
    onFilterSet (filterText) {
      this.moreParams.filter = filterText
      Vue.nextTick( () => this.$refs.vuetable.refresh())
      console.log('filter-set', filterText)
    },
    onFilterReset () {
      delete this.moreParams.filter
      Vue.nextTick( () => this.$refs.vuetable.refresh())
      console.log('filter-reset')
    }
  }
}
</script>



